import React from "react";
import "./filter.scss";
import filters from "./filterdata";

const Filter = ({
  launchYear,
  setLaunchYear,
  successfulLaunch,
  setSuccessfulLaunch,
  successfulLand,
  setSuccessfulLand,
}) => {
  return (
    <React.Fragment>
      <div className="filter">
        <div className="wrapper">
          <div className="filter2">
            <span className="heading">Filters</span>
            <div className="years">
              <span className="heading">Launch Year</span>
              {filters.map((obj, index) => {
                return (
                  <React.Fragment>
                    <div className="each-year">
                      <span
                        className={obj.year == launchYear ? "active" : ""}
                        onClick={() => {
                          setLaunchYear(obj.year);
                        }}
                      >
                        {obj.year}
                      </span>
                    </div>
                  </React.Fragment>
                );
              })}
            </div>
            <div className="launch">
              <span className="heading">Successful Launch</span>
              <div className="option">
                <span
                  className={
                    successfulLaunch === null
                      ? ""
                      : successfulLaunch === true
                      ? "active"
                      : ""
                  }
                  onClick={() => {
                    setSuccessfulLaunch(true);
                  }}
                >
                  True
                </span>
              </div>
              <div className="option">
                <span
                  className={
                    successfulLaunch === null
                      ? ""
                      : successfulLaunch === true
                      ? ""
                      : "active"
                  }
                  onClick={() => {
                    setSuccessfulLaunch(false);
                  }}
                >
                  False
                </span>
              </div>
            </div>
            <div className="land">
              <span className="heading">Successful Landing</span>
              <div className="option">
                <span
                  className={
                    successfulLand === null
                      ? ""
                      : successfulLand === true
                      ? "active"
                      : ""
                  }
                  onClick={() => {
                    setSuccessfulLand(true);
                  }}
                >
                  True
                </span>
              </div>
              <div className="option">
                <span
                  className={
                    successfulLand === null
                      ? ""
                      : successfulLand === true
                      ? ""
                      : "active"
                  }
                  onClick={() => {
                    setSuccessfulLand(false);
                  }}
                >
                  False
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Filter;
