import React, { useEffect, useState } from "react";
import "./info.scss";

const Cards = ({ launchYear, successfulLaunch, successfulLand, data1 }) => {
  return (
    <React.Fragment>
      {data1 === null
        ? "Loading .... "
        : data1.map((obj, index) => {
            return (
              <React.Fragment>
                <div className="card">
                  <div className="each-card">
                    <div className="img">
                      <img src={obj.links.mission_patch_small} alt="" />
                    </div>
                    <div className="heading">
                      <h2>{obj.mission_name}</h2>
                    </div>
                    <div className="info">
                      <p>
                        <strong>Mission Ids: </strong>
                        <span>{obj.mission_id}</span>
                      </p>
                      <p>
                        <strong>Launch Year: </strong>
                        <span>{obj.launch_year}</span>
                      </p>
                      <p>
                        <strong>Successful Launch : </strong>
                        <span>{obj.launch_success ? "True" : "False"}</span>
                      </p>
                      <p>
                        <strong>Successful Landing :</strong>
                        <span>
                          {obj.rocket.first_stage.cores[0].land_success
                            ? "True"
                            : obj.rocket.first_stage.cores[0].landing_intent
                            ? "False"
                            : "No Landing intent"}
                        </span>
                      </p>
                    </div>
                  </div>
                </div>
              </React.Fragment>
            );
          })}
    </React.Fragment>
  );
};

const Info = ({ launchYear, successfulLaunch, successfulLand }) => {
  const [data1, setData1] = useState(null);

  useEffect(() => {
    const xhr = new XMLHttpRequest();

    xhr.open("GET", `https://api.spaceXdata.com/v3/launches?limit=100&launch_success=${successfulLaunch===null?"":successfulLaunch?"true":"false"}&land
    _success=${successfulLand===null?"":successfulLand?"true":"false"}&launch_year=${launchYear===null?"":launchYear}`, true);

    xhr.onload = function () {
      setData1(JSON.parse(xhr.responseText));
    };

    xhr.send();
  }, [launchYear, successfulLaunch, successfulLand]);
  return (
    <React.Fragment>
      <div className="info">
        <div className="wrapper">
          <div className="card-con">
            <Cards
              launchYear={launchYear}
              successfulLaunch={successfulLaunch}
              successfulLand={successfulLand}
              data1={data1}
            />
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Info;
