import React, { useEffect, useState } from "react";
import "./home.scss";
import Filter from "../../components/filter/Filter";
import Info from "../../components/info/Info";

const Home = () => {
  const [launchYear, setLaunchYear] = useState(null);
  const [successfulLaunch, setSuccessfulLaunch] = useState(null);

  const [successfulLand, setSuccessfulLand] = useState(null);

  console.log(launchYear);
  console.log(successfulLaunch);
  console.log(successfulLand);

  return (
    <React.Fragment>
      <div className="home">
        <div className="wrapper">
          <h1>SpaceX Launch Programs</h1>

          <div className="filter-con">
            <Filter
              launchYear={launchYear}
              setLaunchYear={setLaunchYear}
              successfulLaunch={successfulLaunch}
              setSuccessfulLaunch={setSuccessfulLaunch}
              successfulLand={successfulLand}
              setSuccessfulLand={setSuccessfulLand}
            />
          </div>
          <div className="info-con">
            <Info
              launchYear={launchYear}
              successfulLand={successfulLand}
              successfulLaunch={successfulLaunch}
            />
          </div>

          <h3>Developed By : Nitish Paudel</h3>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Home;
